<script>
	import { fade, fly } from "svelte/transition";
    export let covered = true;
    export let transitionDuration = 1000

    function myFly(node, { delay = 0, duration = 1000 }) {
        const o = parseFloat(getComputedStyle(node).height);
        
        return {
            delay,
            duration,
            css: t => {
                return `height: ${t * o}`
                }
        };
    }
</script>

<style> 

    div{
        background-color: black;
        z-index: 1;
    }
    #upperBlack{
        height: 50%;
        width: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
    }

    #lowerBlack{
        height: 50%;
        width: 100%;
        position: absolute;
        bottom: 0px;
        left: 0px;
    }

    #completeBlack{
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
    }
</style>

{#if covered}
    <div
    id="upperBlack"
    out:myFly="{{ duration: transitionDuration }}"
    >
    </div>
    <div
    id="lowerBlack"
    out:myFly="{{ duration: transitionDuration }}"
    >
    </div>
    <div
    id="completeBlack"
    out:fade="{{ duration: transitionDuration }}"
    >
    </div>
{/if}
